import { NgModule, Component } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ProjectsComponent } from './projects/projects.component';
import { AboutComponent } from './about/about.component';
import { ProjectDetailsComponent } from './project-details/project-details.component';
import { BlenderComponent } from './blender/blender.component';

const routes: Routes = [
  { path: '', redirectTo: '/programming', pathMatch: 'full' },
  { path: 'programming', component: ProjectsComponent },
  { path: 'blender', component: BlenderComponent },
  { path: 'about', component: AboutComponent },
  { path: 'details/:name', component: ProjectDetailsComponent },
  { path: '**', redirectTo: '/programming' }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
