import { Component, OnInit, Input } from '@angular/core';
import { BlenderCard } from 'src/app/blenderCard';
import { BlenderCardMovie } from 'src/app/blenderCardMovie';
import * as THREE from 'three';
import { Light } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'

@Component({
  selector: 'app-blender-card',
  templateUrl: './blender-card.component.html',
  styleUrls: ['./blender-card.component.css']
})
export class BlenderCardComponent implements OnInit {
  private gltfLoader: GLTFLoader
  @Input() card: BlenderCard;
  @Input() movie: BlenderCardMovie;
  constructor() { }

  ngOnInit(): void {
    console.log(this.card);
    console.log(this.movie);
    //    this.load3dModel();

  }

  load3dModel() {
    const scene = new THREE.Scene();
    const camera = new THREE.PerspectiveCamera(75, window.innerWidth / window.innerHeight, 0.1, 1000);


    const light = new Light(0xffffff, 1)
    scene.add(light);
    const renderer = new THREE.WebGLRenderer();
    renderer.setSize(window.innerWidth, window.innerHeight);
    document.body.appendChild(renderer.domElement);

    const geometry = new THREE.BoxGeometry(1, 1, 1);
    const material = new THREE.MeshBasicMaterial({ color: 0x00ff00 });
    const cube = new THREE.Mesh(geometry, material);
    scene.add(cube);
    const loader = new GLTFLoader();

    loader.load('../../../assets/gltfs/A6M.glb', function(gltf) {
      console.log(gltf);
      scene.add(gltf.scene);

    }, undefined, function(error) {

      console.error(error);

    });
    camera.position.z = 5;



    function animate() {
      requestAnimationFrame(animate);
      cube.rotation.x += 0.01;
      cube.rotation.y += 0.01;
      renderer.render(scene, camera);
    }
    animate();
  }

}
