import { Component, OnInit } from '@angular/core';
import { BlenderCardService } from '../blender-card.service';
import { BlenderCard } from '../blenderCard';
import { BlenderCardMovie } from '../blenderCardMovie';

@Component({
  selector: 'app-blender',
  templateUrl: './blender.component.html',
  styleUrls: ['./blender.component.css']
})
export class BlenderComponent implements OnInit {

  cards: BlenderCard[];
  movies: BlenderCardMovie[];
  option: String;

  constructor(
    private blenderCardService: BlenderCardService,
  ) { }

  ngOnInit() {
    this.getBlenderCards();
    this.getBlenderCardsMovies();
  }

  getBlenderCards(): void {
    this.blenderCardService.getBlenderCards().subscribe(cards => this.cards = cards);
  }
  getBlenderCardsMovies(): void {
    this.blenderCardService.getBlenderCardMovies().subscribe(movies => this.movies = movies);
  }
  menuSelect(option: String) {
    this.option = option;
  }

}
