import { Injectable } from '@angular/core';
import { BlenderCard } from './blenderCard';
import { BlenderCardMovies, BlenderCards } from './mock-blenderCards';
import { Observable, of } from 'rxjs';
import { BlenderCardMovie } from './blenderCardMovie';

@Injectable({
  providedIn: 'root'
})
export class BlenderCardService {

  constructor() { }

  getBlenderCards(): Observable<BlenderCard[]> {
    return of(BlenderCards);
  }

  getBlenderCardsByName(name: String): BlenderCard {
    return BlenderCards.find(card => card.name === name);
  }
  getBlenderCardMovies(): Observable<BlenderCardMovie[]> {
    return of(BlenderCardMovies);
  }
}
